.setupAccordian .accordion__button:before {
  float: right !important;
}

.setupAccordian .accordion__button[aria-expanded="true"]::before,
.setupAccordian .accordion__button[aria-selected="true"]::before {
  transform: rotate(224deg);
}

.setupAccordian .accordion__button:before {
  transform: rotate(44deg);
}

.setupAccordian .accordion__button {
  background-color: transparent;
  border-bottom: solid 1px rgba(36, 39, 44, 0.12);
  padding-left: 0;
}

.setupAccordian .accordion__button:hover {
  background-color: transparent;
}

.setupAccordian .accordion {
  border: none;
}
